/* #Icon
  -------------------------------------------------------------------------- */
/*doc
---
title: icon
name: icon
categories: [project]
---

アイコンフォントを使ったアイコンを表示します。デフォルトでは`:before`擬似要素で表示されます。
サンプルコードはパンくずリストのホームをアイコンで（2つ目以降はアイコンだけで）示した場合の例です。

装飾的なアイコンは`<span>`と`aria-hidden="true"`でマークアップをします。

意味を伝えたいアイコンは`aria-label=""`でマークアップします。
テキスト全体を読み上げたときに違和感が出ないようにします。

テキストを表示できない場合は`.u-sr-only`でテキストをマークアップします（非表示のままスクリーンリーダーには読み上げられます）。

```block
<div>
  <span class="p-icon p-icon--home" aria-hidden="true"></span>ホーム
</div>

<div>
  <span class="p-icon p-icon--home" aria-label="ホーム"></span>
</div>

<div>
  <span class="p-icon p-icon--home" aria-hidden="true"></span>
  <span class="u-sr-only">ホーム</span>
</div>
```
*/
@font-face {
  font-family: "iconfont";
  src: url("../font/iconfont.eot");
  src: url("../font/iconfont.eot?#iefix") format("eot"), url("../font/iconfont.woff") format("woff"), url("../font/iconfont.ttf") format("truetype"), url("../font/iconfont.svg#iconfont") format("svg");
  font-weight: normal;
  font-style: normal;
}
/* `p-icon p-icon--icon-name`のようなマルチクラスで指定します。 */
.p-icon:before {
  font-family: "iconfont";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  top: -0.1em;
  display: inline-block;
  vertical-align: middle;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  line-height: 1;
  text-decoration: none;
  text-transform: none;
  speak: none;
}

.p-icon--home:before {
  content: "";
}

/* 使用中CSS */
.icon:after {
  display: block;
  content: "";
}

.a-icon {
  position: relative;
  display: inline-block;
  width: 32px;
  height: 30px;
  line-height: 1;
  color: inherit;
  vertical-align: top;
  touch-action: manipulation;
  background-color: transparent;
  border: none;
  appearance: none;
}

.like-icon {
  position: relative;
}
.like-icon:after {
  background-size: 33px 30px;
  width: 33px;
  height: 32px;
  position: absolute;
  right: 0;
  top: 0;
}

.icon-star:after {
  background: url(../../assets/img/icon/star-outline-gray.svg) no-repeat;
}
.icon-starFill:after {
  background: url(../../assets/img/icon/star-outline-yellow.svg) no-repeat;
}
.icon-star-wrap {
  border: 1px solid #D0D0D0;
  border-radius: 8px;
  min-width: 52px;
  height: 52px;
}
.icon-star-wrap:after {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.icon-search {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 61px;
  cursor: pointer;
}
@media screen and (min-width: 1068px) {
